@value makerBlack,makerWhite,makerGrey,makerGreen,makerPink from '../../common/colors.css';

.button {
  background-color: makerGreen;
  color: makerWhite;
  font-weight: bold;
  cursor: pointer;
  padding: 0.5em;
  flex: 1 1 50%;
  font-size: 0.8rem;
  border: 0;
  border-bottom: 1px solid black;
}

.button:hover {
  opacity: 0.8;
}
